@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  min-height: 100vh;
}

#root {
  min-height: 100vh;
}

.container {
  min-height: 100vh;
  background: #fafafa;
  text-align: center;
  color: #262626;
}

.profile img {
  border-radius: 50%;
  width: 200px;
  margin-top: 15px;
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
}

.profile img:hover {
  -webkit-filter: brightness(130%);
          filter: brightness(130%);
}

.profile {
  width: 240px;
  margin: 0 auto;
}

.profile-box {
  will-change: width, height, left, top;
  position: absolute;
}

.introduction {
  margin: auto;
  text-align: center;
  position: relative;
  background: transparent;
  display: block;
}

.highlights img {
  border-radius: 0;
  display: inline-block;
  width: 40px;
  position: absolute;
  z-index: 150;
}

h1 {
  font-size: 30px;
  margin: auto;
  display: inline;
  font-family: 'Atkinson Hyperlegible';
  cursor: default;
}

h2 {
  font-size: 30px;
  margin: auto;
  display: inline;
  font-family: 'Atkinson Hyperlegible';
  cursor: default;
}

h3 {
  font-size: 30px;
  margin: auto;
  display: inline;
  font-family: 'Atkinson Hyperlegible';
  z-index: 100;
  cursor: default;
}

h4 {
  display: block;
  font-size: 15px;
  z-index: 100;
  margin: 0;
  font-family: 'Montserrat';
  cursor: default;
}

h5 {
  display: block;
  position: relative;
  margin: 0 auto;
  font-family: 'Atkinson Hyperlegible';
  font-size: 20px;
}

h6 {
  display: block;
  position: relative;
  margin: 0 auto;
  font-family: 'Atkinson Hyperlegible';
  font-size: 20px;
  padding-bottom: 15px;
}

p {
  display: block;
  position: relative;
  margin: 0 auto;
  padding: 7px 0;
  max-width: 400px;
  z-Index: 50;
}

@use postcss-simple-vars;
@use postcss-nested;

a {
  cursor: pointer;
}

.link-1 {
  position: relative;
  text-decoration: none;
  display: inline-block;
  color: black;
  padding: 0 2px;
  transition: color ease 0.3s;
}

.link-1::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 5%;
  left: 0;
  bottom: 0;
  background-color: #ffb6b6;
  transition: all ease 0.3s;
}

.link-1:hover {
  color: white;
}

.link-1:hover::after {
  height: 100%;
}

.tooltip {
  position: relative;
}

.tooltip-trigger {
  display: inline-block;
}

.tooltip-bubble {
  min-width: 220px;
  max-width: 400px;
  position: absolute;
  z-index: 10;
}

.tooltip-bubble::after {
  content: '';
    position: absolute;
}

.tooltip-top {
  bottom: 100%;
  left: 50%;
  padding-bottom: 9px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.tooltip-top::after {
  border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    border-top: 9px solid rgba(0,0,0,.7);
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

span:hover {
  transition: all 0.3s ease;
  background: lightblue;
}

span {
  margin-bottom:-1px;
  border-bottom: 1px solid #67C8EA;
}

.tooltip-message {
  background: rgba(0,0,0,1);
  border-radius: 15px;
  color: #fff;
  font-size: .75rem;
  line-height: 1.4;
  padding: .75em;
  text-align: center;
}
table {
    position: relative;
    top: -30px;
    margin: 0 auto;
    table-layout: fixed;
    font-family: 'Open Sans';
}

td {
    padding: 0 20px;
}

a svg {
    width: 50px;
}

a svg:hover > path {
    fill: #6a2c70;
}

.employment {
    border-spacing: 0 10px;
}

.employment img {
    max-width: 100px;
    max-height: 100px;
    border-radius: 15px;
    margin: 6px 0;
}

.employment img:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}

.employment td {
    max-width: 300px;
    padding: 0 8px;
    font-size: 13px;
    text-align: left;
}

.employment tr {
    padding-top: 20px;
}

.skills {
    margin: 0 auto;
}

.skills td {
    padding: 15px 5px 5px 0px;
    max-width: 100px;
}

.row2 td{
    padding: 0 5px 15px 0px;
    top: -30px;
}

.projects img {
    max-width: 100px;
    max-height: 100px;
    border-radius: 15px;
    margin: 6px 0;
}

.projects img:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}

.projects td {
    max-width: 300px;
    padding: 0 8px;
    font-size: 13px;
    text-align: left;
}

.about {
    margin-bottom: 5px;
}
